import $ from "jquery";

$(function() {
    var bodyEl = $("body");

    bodyEl.on('click', '.js-callback', function () {
        $('.popup-callback').addClass('_visible');
        bodyScrollLock.disableBodyScroll($(this));
    });

    bodyEl.on('click', '.js-popup-close', function () {
        $('.popup').removeClass('_visible');
        bodyScrollLock.clearAllBodyScrollLocks();
    });

    $(document).mouseup(function (e){
        var popupActive = $(".popup._visible");
        if (popupActive.is(e.target)
            && popupActive.has(e.target).length === 0
            && !$('.popupActive').is(e.target)) {
            $(popupActive).removeClass('_visible');
            bodyScrollLock.clearAllBodyScrollLocks();
        }
    });
});
