import $ from "jquery";

ymaps.ready(init);
function init(){
    var indexMapCenter = [55.720773, 37.770515];
     if ($(window).width() <= 768){
        indexMapCenter = [55.720956, 37.773619];
    }
    var indexMap = new ymaps.Map("map", {
        center: indexMapCenter,
        zoom: 17,
        controls: []
    });
    indexMap.geoObjects.options.set({
        preset: 'islands#Icon',
        iconColor: '#42c1c7'
    });
    indexMap.geoObjects.add(new ymaps.Placemark([55.720956, 37.773619], {balloonContent: 'г. Москва, Рязанский пр-т 24, к. 1'}, {
        iconLayout: 'default#image',
        iconImageHref: '../assets/images/pin.svg',
        iconImageSize: [37, 37],
        iconImageOffset: [0, 0]
    }));
}

