class Popup {    constructor(button) {        this.body = document.body;        this.dataPopup = button.getAttribute('data-modal');        this.popup = document.querySelector(`.${this.dataPopup}`);        this.avtiveClass = 'js-isShowed';        this.button = button;        this.openned = false;    }    open() {        this.button.classList.add(this.avtiveClass);        this.body.style.overflow = 'hidden';        this.popup.classList.add(this.avtiveClass);        this.popup.style.display = 'block';        this.openned = true;    }    close() {        this.button.classList.remove(this.avtiveClass);        this.body.style.overflow = '';        this.popup.classList.remove(this.avtiveClass);        this.popup.style.display = 'none';        this.openned = false;    }    toggle() {        this.openned            ? this.close()            : this.open()    }}(() => {    let buttons = document.querySelectorAll('.p-parameters');    buttons && buttons.forEach(btn => {            let newPopup = new Popup(btn),                popupOverlay = newPopup.popup.querySelector('.popup-parameters__overlay'),                closeButton = newPopup.popup.querySelector('.popup-parameters__close');            if (popupOverlay && closeButton) {                btn.addEventListener('click', (e) => {                    e.preventDefault();                    newPopup.toggle()                });                closeButton.addEventListener('click', () => newPopup.close());                popupOverlay.addEventListener('click', (e) => {                    (e.currentTarget === e.target) ? newPopup.close() : false;                })            }        });})();