import $ from "jquery";

$(function() {
    var indexBanner = new Swiper('.banner', {
        speed: 400,
        spaceBetween: 0,
        loop: true,
        navigation: {
            nextEl: '.banner__control.button-control-next',
            prevEl: '.banner__control.button-control-prev',
        },
    });

    var indexWorks = new Swiper('.works__slider', {
        speed: 400,
        spaceBetween: 0,
        loop: true,
        navigation: {
            nextEl: '.works__control.button-control-next',
            prevEl: '.works__control.button-control-prev',
        },
    });

    var indexReviews = new Swiper('.reviews__slider', {
        speed: 400,
        spaceBetween: 30,
        loop: true,
        navigation: {
            nextEl: '.reviews__control.button-control-next',
            prevEl: '.reviews__control.button-control-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 1
            },
            1024: {
                slidesPerView: 2
            },
            1600: {
                slidesPerView: 3
            },
        }
    });

    var indexPartners = new Swiper('.partners__slider', {
        speed: 400,
        spaceBetween: 0,
        loop: true,
        navigation: {
            nextEl: '.partners__control.button-control-next',
            prevEl: '.partners__control.button-control-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 1
            },
            600: {
                slidesPerView: 2
            },
            768: {
                slidesPerView: 3
            },
            1024: {
                slidesPerView: 4
            },
            1201: {
                slidesPerView: 5
            },
            1601: {
                slidesPerView: 6,
            }
        }
    });
});
