class Sidebar {
    constructor(parent, activeClass, btn) {
        this.parent = parent;
        this.activeClass = activeClass;
        this.bth = btn;
        this.isOpen = false;
        this.isOpenMobile = false;
    }

    open() {
        this.isOpen = true;
        this.parent.classList.add(this.activeClass);
    }

    close() {
        this.isOpen = false;
        this.parent.classList.remove(this.activeClass);
    }

    toggle() {
        this.isOpen ? this.close() : this.open()
    }

    mobileOpen(sidebar) {
        this.isOpenMobile = true;
        sidebar.classList.add('sidebar_open');
        document.body.style.overflow = 'hidden';
    }
    mobileClose(sidebar) {
        this.isOpenMobile = false;
        sidebar.classList.remove('sidebar_open');
        document.body.style.overflow = '';
    }
}

(() => {
    const btnSidebarLevel2 = document.querySelectorAll('.js-sidebar-level2');
    const btnSidebarLevel3 = document.querySelectorAll('.js-sidebar-level3');
    const sidebarOpen = document.querySelector('.sidebar-open');
    const sidebarClose = document.querySelector('.sidebar__close');
    const sidebarMenu = document.querySelector('.sidebar');

    btnSidebarLevel2 && btnSidebarLevel2.forEach(btn => {
        const sidebar = new Sidebar(btn.closest('.sidebar__item'), 'sidebar__item_open', btn);
        btn && btn.addEventListener('click', () => sidebar.toggle())
    });

    btnSidebarLevel3 && btnSidebarLevel3.forEach(btn => {
        const sidebar = new Sidebar(btn.closest('.sidebar__level2-item'), 'sidebar__level2-item_open', btn);
        btn && btn.addEventListener('click', () => sidebar.toggle())
    });

    sidebarOpen && sidebarMenu && sidebarOpen.addEventListener('click', () => {
        const sidebar = new Sidebar();
        sidebar.mobileOpen(sidebarMenu);
    });

    sidebarClose && sidebarMenu && sidebarClose.addEventListener('click', () => {
        const sidebar = new Sidebar();
        sidebar.mobileClose(sidebarMenu);
    })
})();
