import $ from "jquery";
$(function() {
    function burgerContent() {
        if(window.innerWidth <= 1024) {
            $('.header__contacts').appendTo('.header__nav');
            $('.header__socials').appendTo('.header__nav');
        }
        else if (window.innerWidth > 1024) {
            $('.header__contacts').insertAfter('.header__search');
            $('.header__socials').insertAfter('.header__contacts');
        }
    }

    function showOverlay() {
        var $overlay = $('.overlay');
        if ($overlay.is(':animated')) {
            return false;
        }
        $overlay.fadeIn(600);
        return true;
    }

    function hideOverlay() {
        var $overlay = $('.overlay');
        if ($overlay.is(':animated')) {
            return false;
        }
        $overlay.fadeOut(600);
        bodyScrollLock.clearAllBodyScrollLocks();
        return true;
    }
    function burger() {
        if(window.innerWidth <= 1024) {
            var bodyEl = $("body");
            bodyEl.on('click', ".js-burger", function () {
                if($(this).hasClass("burger-opened")){
                    $(this).removeClass("burger-opened");
                    hideOverlay();
                }
                else {
                    $(this).addClass("burger-opened");
                    bodyScrollLock.disableBodyScroll($(this));
                    showOverlay();
                }
            });
            bodyEl.on('click', '.overlay', function () {
                $('.js-burger').removeClass('burger-opened');
                hideOverlay();
            });
        }
    }
    burger();
    burgerContent();
    $(window).on("resize", function (){
        burger();
        burgerContent();
    })
});