import $ from "jquery";

$(function() {
    $(".input").keyup(function() {
        var inputFilled = $(this).val();
        if(inputFilled) {
            $(this).addClass("input-active");
        }
        else {
            $(this).removeClass("input-active");
        }
    });

    $('body').on('click', '.js-search', function (e) {
        e.preventDefault();
        e.stopPropagation();
        var searchInput = $('.header__search_input');
        if(searchInput.val()){
            $('.header__search').submit();
        }
        else {
            searchInput.toggleClass('input-active');
        }
    });

    $(document).mouseup(function (e){
        var searchInput = $(".header__search_input");
        if (!searchInput.is(e.target)
            && !$('.js-search').is(e.target)
            && !searchInput.val()) {
            searchInput.removeClass('input-active');
        }
    });

    $('.js-phone-mask').mask("+7 (999) 999 99 99", {placeholder:"_"});

});