import $ from "jquery";

$(function() {
    var btn = $('.js-scroll-up');

    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
            btn.addClass('_visible');
        } else {
            btn.removeClass('_visible');
        }
    });

    btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
    });
});
